$(document).ready(() => {
	// Gallery Images
	// img-wrap class is used to copy image src to anchore tag
	// href value to activate lightbox
	$('.image-wrap').each(function() {
		$(this).attr(
			'href',
			$(this)
				.find('img')
				.attr('src')
		);
	});

	// LOad More Content
	// Load more baby images

	$('.image-wrap').hide();
	$('.image-wrap')
		.slice(0, 12)
		.show();
	$('.loadMore').on('click', function(e) {
		e.preventDefault();
		// Assign the result of the media query to a variable

		// Check if the media query is a match

		var x = window.matchMedia('(max-width: 768px)');
		if (x.matches) {
			$('.image-wrap:hidden')
				.slice(0, 4)
				.slideDown(1000);
		} else {
			$('.image-wrap:hidden')
				.slice(0, 6)
				.slideDown(1000);
		}

		if ($('.image-wrap:hidden').length == 0) {
			$('.loadMore').text('No More Content');
		}
	});
});
